@import "_normalize";
@import "_variables";


html {
   box-sizing: border-box;
}

*,
*:before,
*:after {
   box-sizing: inherit;
}

body {
   background: rgb(211, 224, 224);
   background: linear-gradient(180deg, rgba(211, 224, 224, 1) 0%, rgba(235, 217, 178, 1) 100%);
}

.sectionAllign {
   padding: 0 16px;
}

// navigation

.navigation {
   &__list {
      list-style: none;
      padding: 0;
      display: flex;
      justify-content: center;
      align-items: center;

      &--visible {
         @media (max-width: 730px) {
            display: flex;
         }
      }

      @media (max-width: 730px) {
         display: none;
         flex-direction: column;
      }

   }

   &__item {
      display: inline; //elementy obok siebie (bez flexa)
      display: block; //dla flexa
      margin: 0 16px;
   }

   &__link {
      text-decoration: none;
      color: #1A936F;

      &--bigger {
         font-family: "Ubuntu";
         font-size: 2.5rem;
         font-weight: bold;
      }

      &--smaller {
         font-family: "Ubuntu";
         font-size: 1.5rem;
         font-weight: bold;
      }

   }

   &__link:hover {
      color: hotpink;
   }


   &__switcher {
      @media (max-width: 730px) {
         border: solid 1px;
         background: none;
         position: absolute;
         right: 16px;
         top: 16px;
      }
   }
}


.site-header {
   &:hover {
      background: black;
   }

   &-scr {
      background-image: url("../assets/wind-farm.jpg");
      background-position: center;
      background-origin: border-box;
      background-repeat: no-repeat;
      height: 400px;
      max-width: 1024px;
      margin: 92px auto 0;
      width: 100%;
      margin-top: 92px;
      position: relative;
      z-index: -1;
   }
}


.kwadrat {
   background: rgba(23, 29, 29, 0.493);
   width: 200px;
   height: 150px;
   color: white;
   position: absolute;
   right: 100px;
   bottom: 0px;
   padding: 16px;

   &__h5 {
      padding: 0;
      color: tomato;
      margin: 0;
   }

   &__contekst {
      margin: 0;
   }
}


.contentGrid {
   display: grid;
   grid-template-columns: 1fr 1fr;
   grid-column-gap: 16px;
   grid-row-gap: 16px;
}

@media (max-width: 640px) {
   .contentGrid {
      grid-template-columns: 1fr;
   }
}

